.pos {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.pos-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.pos-header h1 {
  color: #1f2937;
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
}

.pos-user {
  color: #6b7280;
  font-size: 0.875rem;
}

.pos-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  height: calc(100vh - 200px);
}

.pos-products {
  background: white;
  border-radius: 0.5rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

.pos-products h2 {
  color: #1f2937;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0 0 1rem 0;
}

.pos-filters {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding: 1rem;
  background: #f9fafb;
  border-radius: 0.375rem;
  border: 1px solid #e5e7eb;
}

.pos-filters .filter-group {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.pos-filters .filter-group label {
  font-weight: 500;
  color: #374151;
  font-size: 0.75rem;
}

.pos-filters .filter-group select,
.pos-filters .filter-group input {
  padding: 0.5rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  background: white;
}

.pos-filters .filter-group input {
  min-width: 200px;
}

/* Responsive design for POS filters */
@media (max-width: 768px) {
  .pos-filters {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .pos-filters .filter-group input {
    min-width: 100%;
  }
  
  .quantity-input {
    width: 2.5rem;
    font-size: 0.875rem;
  }
  
  .quantity.clickable {
    padding: 0.5rem;
    min-width: 2.5rem;
  }
  
  /* Responsive table layout */
  .cart-table-header {
    display: none;
  }
  
  .cart-item {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    padding: 1rem;
    border: 1px solid #e5e7eb;
    border-radius: 0.375rem;
    margin-bottom: 0.5rem;
    background: white;
  }
  
  .item-product {
    order: 1;
  }
  
  .item-price {
    order: 2;
    text-align: left;
    font-size: 0.875rem;
  }
  
  .item-quantity {
    order: 3;
    justify-content: flex-start;
  }
  
  .item-total {
    order: 4;
    text-align: left;
    font-size: 1rem;
  }
  
  .item-actions {
    order: 5;
    justify-content: flex-end;
  }
}

.products-info {
  margin-bottom: 1rem;
}

.products-count {
  color: #6b7280;
  font-size: 0.875rem;
  margin: 0;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.product-card {
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  background: white;
}

.product-card:hover {
  border-color: #3b82f6;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transform: translateY(-2px);
}

.product-card.out-of-stock {
  opacity: 0.5;
  cursor: not-allowed;
  background-color: #f9fafb;
}

.product-info h3 {
  color: #1f2937;
  font-size: 0.875rem;
  font-weight: 600;
  margin: 0 0 0.25rem 0;
}

.product-sku {
  color: #6b7280;
  font-size: 0.75rem;
  margin: 0 0 0.5rem 0;
}

.product-price {
  color: #059669;
  font-size: 1rem;
  font-weight: 700;
  margin: 0 0 0.25rem 0;
}

.product-stock {
  color: #6b7280;
  font-size: 0.75rem;
  margin: 0;
}

.pos-cart {
  background: white;
  border-radius: 0.5rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.cart-header h2 {
  color: #1f2937;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
}

.cart-items {
  flex: 1;
  margin-bottom: 1rem;
}

.empty-cart {
  color: #6b7280;
  text-align: center;
  padding: 2rem;
  font-style: italic;
}

.cart-table-header {
  display: grid;
  grid-template-columns: 2fr 1fr 1.5fr 1fr 0.5fr;
  gap: 1rem;
  padding: 0.75rem 1rem;
  background: #f3f4f6;
  border-radius: 0.375rem 0.375rem 0 0;
  font-weight: 600;
  font-size: 0.875rem;
  color: #374151;
  border: 1px solid #e5e7eb;
  border-bottom: none;
}

.cart-item {
  display: grid;
  grid-template-columns: 2fr 1fr 1.5fr 1fr 0.5fr;
  gap: 1rem;
  align-items: center;
  padding: 1rem;
  border: 1px solid #e5e7eb;
  border-top: none;
  background: white;
  transition: background-color 0.2s ease;
}

.cart-item:last-child {
  border-radius: 0 0 0.375rem 0.375rem;
}

.cart-item:hover {
  background: #f9fafb;
}

.item-product {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.item-product h4 {
  color: #1f2937;
  font-size: 0.875rem;
  font-weight: 600;
  margin: 0;
  line-height: 1.2;
}

.item-sku {
  color: #6b7280;
  font-size: 0.75rem;
  margin: 0;
  font-family: monospace;
}

.item-price {
  font-weight: 600;
  color: #1f2937;
  text-align: center;
}

.item-quantity {
  display: flex;
  justify-content: center;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.quantity {
  min-width: 2rem;
  text-align: center;
  font-weight: 600;
  color: #1f2937;
  transition: all 0.2s ease;
}

.quantity.clickable {
  cursor: pointer;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  border: 1px solid transparent;
  background: #f3f4f6;
}

.quantity.clickable:hover {
  background: #e5e7eb;
  border-color: #3b82f6;
  color: #3b82f6;
}

.quantity-edit {
  display: flex;
  align-items: center;
}

.quantity-input {
  width: 3rem;
  padding: 0.25rem 0.5rem;
  border: 1px solid #3b82f6;
  border-radius: 0.25rem;
  text-align: center;
  font-weight: 600;
  color: #1f2937;
  background: white;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.quantity-input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.2);
}

.item-total {
  font-weight: 700;
  color: #059669;
  text-align: center;
  font-size: 0.875rem;
}

.item-actions {
  display: flex;
  justify-content: center;
}

.cart-summary {
  border-top: 1px solid #e5e7eb;
  padding-top: 1rem;
  margin-bottom: 1rem;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  color: #374151;
}

.summary-row.total {
  font-weight: 700;
  font-size: 1.125rem;
  color: #1f2937;
  border-top: 1px solid #e5e7eb;
  padding-top: 0.5rem;
  margin-top: 0.5rem;
}

.summary-row.cost-breakdown {
  color: #6b7280;
  font-size: 0.875rem;
  padding-left: 1rem;
}

.summary-row.tax-breakdown {
  color: #dc2626;
  font-size: 0.875rem;
  padding-left: 1rem;
}

.customer-info {
  margin-bottom: 1rem;
}

.customer-info h3 {
  color: #1f2937;
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 1rem 0;
}

.form-group {
  margin-bottom: 0.75rem;
}

.form-group input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  font-size: 0.875rem;
}

.form-group input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.payment-section {
  margin-bottom: 1rem;
}

.payment-section h3 {
  color: #1f2937;
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 1rem 0;
}

.payment-methods {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
}

.payment-method {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.payment-method:hover {
  border-color: #3b82f6;
  background-color: #f8fafc;
}

.payment-method input[type="radio"] {
  margin: 0;
}

.payment-method input[type="radio"]:checked + span {
  color: #3b82f6;
  font-weight: 600;
}

.checkout-button {
  width: 100%;
  margin-top: 1rem;
}

.error-message {
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  padding: 0.75rem;
  border-radius: 0.375rem;
  margin-bottom: 1rem;
  font-size: 0.875rem;
}

.pos-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem;
  color: #6b7280;
}

.pos-loading .spinner {
  width: 3rem;
  height: 3rem;
  border: 3px solid #e5e7eb;
  border-top: 3px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 1024px) {
  .pos-content {
    grid-template-columns: 1fr;
    height: auto;
  }
  
  .pos-cart {
    max-height: 600px;
  }
}

@media (max-width: 900px) {
  .cart-table-header {
    grid-template-columns: 1.5fr 0.8fr 1.2fr 0.8fr 0.5fr;
    font-size: 0.75rem;
    padding: 0.5rem 0.75rem;
  }
  
  .cart-item {
    grid-template-columns: 1.5fr 0.8fr 1.2fr 0.8fr 0.5fr;
    padding: 0.75rem;
    gap: 0.75rem;
  }
  
  .item-product h4 {
    font-size: 0.8rem;
  }
  
  .item-sku {
    font-size: 0.7rem;
  }
  
  .item-price {
    font-size: 0.8rem;
  }
  
  .item-total {
    font-size: 0.8rem;
  }
}

@media (max-width: 768px) {
  .pos {
    padding: 1rem;
  }
  
  .pos-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
  
  .products-grid {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  }
  
  .cart-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
  
  .item-controls {
    align-self: stretch;
    justify-content: space-between;
  }
  
  .payment-methods {
    grid-template-columns: 1fr;
  }
}
